<template>
    <div>12.23路由
        <router-link :to="`/login/${id}`">login</router-link>
        <button @click="btn">登录</button>
        <button @click="btn2">登录2</button>
        <router-link :to="{path:'/login',query:{username:'123456'}}">query1</router-link>
        <button @click="btn3">query2</button>
        <button @click="btn4">query4</button>
        <router-link to="/login/a">a子路由组件</router-link>
        <button @click="btnb">b子路由组件</button>
    </div>
</template>

<script>
export default {
  data () {
    return {
      id: 1110
    }
  },
  methods: {
    btn () {
      this.$router.push({
        path: `/login/${123456}`
      })
    },
    btn2 () {
      this.$router.push({
        name: 'Login',
        params: {
          id: 123
        }
      })
    },
    btn3 () {
      this.$router.push({
        path: '/login',
        query: {
          name: '4564879'
        }
      })
    },
    btn4 () {
      this.$router.push({
        name: 'Login',
        query: {
          name: '9876542123'
        }
      })
    },
    btnb () {
      this.$router.push({
        path: '/login/b'
      })
    }
  }
}
</script>



