<template>
  <div>
    12.21
    <input type="text" v-ziding1>
    <input type="text" v-focus value="123">
  </div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import mymixin from './aa.js'
export default {
  data () {
    return {}
  },
  mixins: [mymixin],
  directives: {
    focus: {
      inserted: function (el, binding) {
        el.focus()
        console.log(binding.name, binding.value)
      }
    }
  },
  mounted () {
    console.log(mymixin.template)
    axios({
      url: 'http://www.qhdlink-student.top/student/banner.php',
      method: 'post',
      data: qs.stringify({username: 'yx71', userpwd: '123456', userclass: 71, type: 3})
    }).then((res) => {
      console.log(res.data)
    })
  }
}
</script>



