<template>
  <div>
   vuex{{getMsg}}
   <button @click="click1(123)">点击</button>
   {{getTotal}}
   <p>{{msg}}</p>{{num}}
   <button @click="asyncChangeMsg(789)">点击2</button>
   {{getPrice}}
  </div>
</template>

<script>
import {mapState, mapActions, mapMutations, mapGetters} from 'vuex'
export default {
  data () {
    return {
      a: 0
    }
  },
  computed: {
    ...mapState([
      'msg',
      'num'
    ]),
    ...mapGetters([
      'getPrice'
    ]),
    getMsg () {
      return this.$store.state.msg
    },
    getTotal () {
      return this.$store.getters.getPrice
    }
  },
  methods: {
    click1 (a) {
      // this.$store.commit('changeMsg', '传值')
      this.$store.dispatch('asyncChangeMsg', this.a)
    },
    ...mapActions([
      'asyncChangeMsg'
    ]),
    ...mapMutations({
      aaa: 'changeMsg'
    })
  }

}
</script>

<style scoped>

</style>



