<template>
  <div>
    <el-container>
        <el-header :style="{'background':'green'}" id="h1">教师学生管理系统</el-header>
        <el-main>
            <el-container>
                <el-aside :style="{'background':'#888'}">
<el-row class="tac">
  <el-col :span="24">
    <h5>自定义颜色</h5>
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="1">
          <i class="el-icon-menu"></i>
        <span slot="title">导航一</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">导航二</span>
      </el-menu-item>
      <el-menu-item index="3" disabled>
        <i class="el-icon-document"></i>
        <span slot="title">导航三</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">导航四</span>
      </el-menu-item>
    </el-menu>
  </el-col>
</el-row>
                </el-aside>
                <el-main :style="{'background':'#999'}"><right/>
                </el-main>
            </el-container>
        </el-main>
    </el-container>
  </div>
</template>

<script>
import right from './righttable.vue'
export default {
  data () {
    return {
    }
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    }
  },
  components: {
    right
  }
}
</script>

<style  scoped>
#h1{
  font:  bold 26px 宋体;
  text-align: left;
}
</style>



