<template>
  <div>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect" active-text-color="#fd5103" text-color="#333" >
      <img :src="data.logo[0].img" id="img0">
      <el-menu-item v-for="(val,ind) in data.nav" :key="ind" :index="(ind+1).toString()">{{val.first}}</el-menu-item>
      <div v-for="(val,ind) in data.logo.splice(1)" :key="ind+10" :class="'div'+ind">
        <img :src="val.img" alt="" :id="'img'+(ind+1)">
        <div :id="'imga'+ind" :style="{'display':'none','zIndex':'10'}">
          <img :src="data.img[ind].img" alt="">
        </div>
      </div>
    </el-menu>
    <!-- <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <template v-for="(val,ind) in nav">
        <el-menu-item v-if="!val.second" :index="(ind+1).toString()" :key="ind+10">{{val.first}}</el-menu-item>
        <el-submenu v-else :index="(ind+1).toString()" :key="ind+10">{{val.first}}</el-submenu>
      </template>
    </el-menu> -->
  </div>
</template>

<script>
import aa from './data.js'
export default {
  data () {
    return {
      data: {},
      activeIndex: '1',
      show1: false,
      img0: false,
      img1: true
      // nav: [{first: '1', id: 1}, {first: '2', id: 2}, {first: '3', id: 3, second: [{sname: 3.1, id: 3.1}, {sname: 3.2, id: 3.2}]}]
    }
  },
  mixins: [aa],
  mounted () {
    aa.mock().then(res => { this.data = res.data })
  },
  updated () {
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    }
  },
  computed: {
    isShow0 () {
      return this.show1
    }
  }
}
</script>

<style scoped>
.el-menu{
  /* margin-top: 20px; */
  height: 80px;
}
#img0{
  position: absolute;
  left: 28px;
  width: 160px;
  height: 50px;
  top: 15px;
}
li{
  position: relative;
  left: 700px;
}
.div0,.div1{
  width: 16px;
  height: 14px;
  position: absolute;
  top: 35px;
}
.div0{
  right: 110px;
}
.div1{
  right: 60px;
}
#img1,#img2{
  width: 16px;
  height: 14px;
}
.el-menu>.el-menu-item{
  margin-left: 4px;
  margin-right: 4px;
  height: 80px;
  line-height: 80px;
  font-size: 16px;
}
#imga0 img,#imga1 img{
  margin-top: 10px;
  margin-left: 5px;
  width: 100px;
  height: 100px;
}
#imga0,#imga1{
  width: 120px;
  height: 120px;
  background: grey;
  border-radius: 10px;
  position: absolute;
  right: -50px;
}
.div1:hover div,.div0:hover div{
  display: block !important;
}
</style>



