<template>
  <div>
    <el-row>
      <el-col :span="20" :offset="2">
        <el-container>
          <el-header height="100px">
            <h2 class="h1">加盟优势</h2>
            <p class="p1">目前，茶小茗开放了特许经营加盟，您可以阅读我们的加盟信息或联系我们，真诚地期待与您的合作！</p>
          </el-header>
          <el-main>
            <el-row class="e1">
              <el-col :span="5" v-for="val in data" :key="val.h1">
                <img :src="val.img" alt="" width="64px" height="64px">
                <div>{{val.h1}}</div>
                <p>{{val.h2}}</p>
              </el-col>
            </el-row>
          </el-main>
        </el-container>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default{
  data () {
    return {
      data: [{
        'img': '../../static/images/homeIcon1.320524d1.png',
        'h1': '完善的运营管理系统',
        'h2': '茶小茗拥有一批高素质、高效率的运营服务团队， 能够为特许经营商及时解决从前期投资指 导、店面选址到后期经营管理模式、策划宣传 活动等各方面问题。'
      }, {
        'img': '../../static/images/index.png',
        'h1': '持续的产品开发',
        'h2': '茶小茗团队注重产品研发，关注市场变化趋势，及时把握消费者反馈，不断更新推出产品，让品牌保持创新活力。'
      }, {
        'img': '../../static/images/index2.png',
        'h1': '规模的采购与配送',
        'h2': '茶小茗具有完整的供应链体系，核心原料自产，门店统一供货，拥有较高的议价能力，保证产品质量的同时降低成本，提高特许经营商利润。'
      }, {
        'img': '../../static/images/index3.png',
        'h1': '强大的品牌宣传',
        'h2': '总部将投入丰富的品牌营销活动，不断扩大品影响力，包括新媒体的品牌推广活动与传统的公共媒体广告支持。'
      }]
    }
  }
}
</script>

<style scoped>
.h1{
  margin-top: 20px;
}
.p1{
  color: #09ac71;
  font-size: 8px;
  margin-top: -10px;
}
.e1{
  display: flex;
  justify-content: space-between;
}
.e1 div div{
  color: #09ac71;
  margin-top: 10px;
}
.e1 p{
  margin-top: 18px;
  color: #333;
  font-size: 13px;
  line-height: 20px;
}
</style>



