<template>
  <div>
    <el-row>
      <el-col :span="20" :offset="2">
        <el-container>
          <el-header height="100px">
            <h2 class="h1">品牌形象</h2>
            <p class="p1">茶小茗以小茗大满贯桶装奶茶作为门店招牌茶饮，依据市场动向不断进行产品线优化。</p>
          </el-header>
          <el-main>
            <img v-for="val in data" :src="val.img" alt="" :key="val.img" width="295px" height="232px">
          </el-main>
        </el-container>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import aa from './data.js'
export default{
  data () {
    return {
      data: []
    }
  },
  mixins: [aa],
  created () {
    aa.mock().then(res => { this.data = res.data.xingxiang })
  }
}
</script>

<style scoped>
.h1{
  margin-top: 20px;
}
.p1{
  color: #09ac71;
  font-size: 8px;
  margin-top: -10px;
}
img{
  margin-left: 30px;
  margin-right: 30px;
  margin-bottom: 25px;
  box-shadow: 0px 0px 10px 0px rgba(0,0,0,0.2);
}
.el-main{
  justify-content: space-between;
}
</style>



