<template>
  <div>
      <!-- :css="false" 可以指定不加载css样式 -->
      <transition  @before-leave="beforeLeave" @leave="leave" @after-leave="afterLeave" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter" :css="false">
          <p v-if="isshow">显示或隐藏</p>
      </transition>
      <button @click="isshow=!isshow">点击</button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isshow: true
    }
  },
  methods: {
    beforeLeave: function (el) {
      console.log('离开前')
      el.style.opacity = 1
    },
    leave: function (el, done) {
      console.log('离开进行中')
      el.style.transition = 'all 3s'
      el.style.opacity = 0
      setTimeout(done, 3000)
    },
    afterLeave: function (el) {
      console.log('离开结束')
    },
    beforeEnter: function (el) {
      console.log('进入前')
      el.style.opacity = 0
    },
    enter: function (el, done) {
      // el.offsetWidth引起浏览器的重回
      // eslint-disable-next-line no-unused-expressions
      el.offsetWidth
      el.style.transition = 'all 3s'
      console.log('进入过程中')
      el.style.opacity = 1
      setTimeout(done, 3000)
    },
    afterEnter: function (el) {
      console.log('进入结束')
    }
  }
}
</script>

<style scoped>
p{
    width: 200px;
    height: 200px;
    background: greenyellow;
}
/* .fade-enter,.fade-leave-active{
    background: red;
    opacity: 0;
} */
/* @keyframes anima{
    0%{
        transform: translate(0,0);
        opacity: 1;
    }
    50%{
        transform: translate(80px,0);
        opacity: 0.5;
    }
    100%{
        transform: translate(0,0);
        background: red;
        opacity: 0;
    }
}
@keyframes anima2{
    0%{
        transform: translate(0,0);
        opacity: 0;
        background: red;
    }
    50%{
        transform: translate(80px,0);
        opacity: 0.5;
    }
    100%{
        transform: translate(0,0);
        opacity: 1;
    }
}
.fade-leave-active{
    animation: anima 1s;
}
.fade-enter-active{
    animation: anima2 1s;
} */
/* .fade-leave-active,.fade-enter-active{
  transition: all 5s;
} */
</style>



