<template>
  <div class="block">
    <el-carousel height="650px">
      <el-carousel-item v-for="item in data" :key="item.img">
        <img :src="item.img" alt="" width="100%">
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
import aa from './data.js'
export default {
  data () {
    return {
      data: []
    }
  },
  mixins: [aa],
  mounted () {
    aa.mock().then(res => { this.data = res.data.banner })
  }
}
</script>

<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
     background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  }
</style>



