        <h2 class="h1">关于我们</h2>
        <p class="p1">你好，我是茶小茗，致力成为一杯好茶的代言人和传播者！</p>
      </el-header>
      <el-footer :style="{'height':'260px'}">
        <el-container>
          <el-aisde>
            <img src="../../static/images/211115042306131324.jpg" alt="" width="569px" height="200px">
