            <h2 class="h2">茶小铭<span class="s1">MEANS’&nbsp;&nbsp;TEA</span></h2>
            <p class="p2">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;2005年，一家名叫旺客的奶茶店诞生于济南，用一杯真材好料、价格公道的奶茶，迅速征服消费者，风靡全国，至2008年全国开出300余家连锁店。扩张的速度令创始团队始料未及，管理系统无法跟上，为了确保每个合作伙伴的利益，创始人石总按下了品牌发展的暂停键，潜下心来修炼内功，以期走的更远。初心不改，10年磨一剑。秉持这份初衷，一个全新的品牌”茶小茗“就这样诞生了...</p>
