<template>
  <div>
    <el-container>
      <el-header>
        <nav1/>
      </el-header>
      <el-footer>
        <banner1/>
      </el-footer>
    </el-container>
    <aboutus/>
    <fuwu/>
    <youshi/>
    <xingxiang/>
    <foot/>
  </div>
</template>

<script>
import aa from './data.js'
import nav1 from './nav.vue'
import banner1 from './banner.vue'
import aboutus from './aboutus.vue'
import fuwu from './fuwu.vue'
import youshi from './youshi.vue'
import xingxiang from './xingxiang.vue'
import foot from './foot.vue'
export default {
  data () {
    return {
      data: {}
    }
  },
  components: {
    nav1,
    banner1,
    aboutus,
    fuwu,
    youshi,
    xingxiang,
    foot
  },
  mixins: [aa]
}
</script>

<style scoped>
.el-header{
  height: 80px !important;
}
.el-footer{
  height: 650px !important;
}
</style>



