<template>
  <div>
    <el-row>
      <el-col :span="22" :offset="1">
        <ul>
          <li v-for="val in data" :key="val.first+'1'">
            <a href="#">{{val.first}}</a>
          </li>
        </ul>
        <el-row :style="{'textAlign':'left'}">
          <el-col :span="6" >
            <img src="../../static\images\footerLogo.59868582.png" alt="" width="192px" height="64px">
          </el-col>
          <el-col :span="6">
            <p>电话：400-157-9588</p>
            <p>邮箱：2523351364@QQ.COM</p>
          </el-col>
          <el-col :span="6">
            <p>地址：山东省济南市历下区文化东路121号</p>
          </el-col>
          <el-col :span="6" :style="{'textAlign':'right'}">
            <img src="../../static\images\footerErweima.758dcbbb.jpg" alt="" height="80px" width="80px">
            <p>关注茶小茗</p>
          </el-col>
        </el-row>
        <p class="p2">© 2020 山东茶小茗餐饮管理有限公司版权所有 鲁ICP备20030825号-1</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import aa from './data.js'
export default{
  data () {
    return {
      data: []
    }
  },
  mixins: [aa],
  created () {
    aa.mock().then(res => { this.data = res.data.nav })
  }
}
</script>

<style scoped>
div{
  background: #242628;
}
ul{
  list-style-type: none;
  height: 80px;
  border-bottom: solid 1px rgba(255, 255, 255, 0.4);
  padding: 0;
}
li{
  display: block;
  float: left;
  margin: 20px;
}
a{
  text-decoration: none;
  color: #fff;
  font-size: 16px;
}
p{
  color: #fff;
  font-size: 13px;
}
.p2{
  color:#7b7c7c;
  font-size: 10px;
  text-align: left;
  margin-bottom: 100px;
}
</style>



